// h1~h6 margin
@mixin header-margin {
  > h1, > h2 {
    margin-top: 3rem;
  }

  > h3, > h4, > h5, > h6 {
    margin-top: 2rem;
  }

  > h1, > h2 {
    &:first-of-type {
      margin-top: 1rem;
    }
  }
}

// font-size
@mixin header-font-size {
  > h1 {
    font-size: 1.75rem;
  }

  > h2 {
    font-size: 1.5rem;
  }

  > h3 {
    font-size: 1.25rem;
  }

  > h4 {
    font-size: 1.125rem;
  }
}

