@mixin header-anchor {
  > h1, > h2, > h3, > h4, > h5, > h6 {
    .anchor-header {
      display: inline-block;
      height:     5.5rem;
      margin-top: -5.5rem;
      visibility: hidden;
    }

    .anchor-link {
      padding-left: 0.375rem;
      opacity:      0;
    }

    &:hover > .anchor-link,
    .anchor-link:focus {
      opacity: 1;
    }
  }
}
